function BounceTo(data) {
    var extent = findExtent(data);
    var pos1 = getESPG(extent[0], extent[1]);
    var pos2 = getESPG(extent[2], extent[3]);
    extent = [pos1[0], pos1[1], pos2[0], pos2[1]];
    var resolution = map.getView().getResolutionForExtent(extent);
    var zoom = map.getView().getZoomForResolution(resolution);
    var center = ol.extent.getCenter(extent);
    map.getView().animate({
        center: center,
        duration: 200
    });
    map.getView().animate({
        zoom: zoom - 1,
        duration: 200 / 2
    }, {
        zoom: zoom,
        duration: 200 / 2
    });
}

function PanTo(location) {
    var extent = findExtent(data);
    var pos1 = getESPG(extent[0], extent[1]);
    var pos2 = getESPG(extent[2], extent[3]);
    extent = [pos1[0], pos1[1], pos2[0], pos2[1]];
    var resolution = map.getView().getResolutionForExtent(extent);
    var zoom = map.getView().getZoomForResolution(resolution);
    var center = ol.extent.getCenter(extent);
    map.getView().animate({
        center: center,
        duration: 200
    });
    map.getView().animate({
        zoom: zoom,
        duration: 200
    });
}

function findCenter(data) {
    var res = [];
    for (var i = 0; i < data.length; i++) {
        res[0] += data[i].x;
        res[1] += data[i].y;
    }
    res[0] /= data.length;
    res[1] /= data.length;
    return res;
}

function findExtent(data) {
    var res = [];
    res[0] = 1000;
    res[1] = 1000;
    res[2] = -1000;
    res[3] = -1000;
    for (var i = 0; i < data.length; i++) {
        if (res[0] < data[i].x)
            res[0] = data[i].x;
        else if (res[2] > data[i].x)
            res[2] = data[i].x;
        if (res[1] < data[i].y)
            res[1] = data[i].y;
        else if (res[3] > data[i].y)
            res[3] = data[i].y;
    }
    return res;
}

