//#region Search Animator
var allSearchSuggestionsCreated = [];
var ActiveSuggs = 0;
var prvTime;

function addtoSearchPrediction(data, invalidatePrevious) {
    const time = Date.now();
    prvTime = time;
    if ((window.innerHeight - 50) < (data.length * 41))
        document.querySelector("#suggestions").style.overflowY = "auto";
    else
        document.querySelector("#suggestions").style.overflowY = "hidden";

    if (data.length > allSearchSuggestionsCreated.length) {
        document.querySelector("#suggestions").style.height = (data.length * 41) + "px";
        //document.querySelector("#suggestions").style.maxHeight = (data.length * 41) + "px";
        var times = (data.length - allSearchSuggestionsCreated.length);
        for (var i = 0; i < times; i++) {
            const nitem = document.createElement("div");
            nitem.classList = "suggestion";
            nitem.innerHTML += "<div class=\"icon\"></div><div class=\"content\">" + data[i].content + "</div>";
            document.querySelector("#suggestions").appendChild(nitem);
            allSearchSuggestionsCreated.push(new Suggestion(data[i].icon, data[i].content, nitem, false, data[i].id));
        }
    } else {
        if (ActiveSuggs < data.length) {
            document.querySelector("#suggestions").style.height = (data.length * 41) + "px";
            //document.querySelector("#suggestions").style.maxHeight = (data.length * 41) + "px";
        } else {
            setTimeout(() => {
                if (time == prvTime)
                    document.querySelector("#suggestions").style.height = (data.length * 41) + "px";
                //document.querySelector("#suggestions").style.maxHeight = (data.length * 41) + "px";
            }, 200);
        }
    }
    ActiveSuggs = data.length;
    for (var i = 0; i < allSearchSuggestionsCreated.length; i++) {
        if (i < data.length) {
            allSearchSuggestionsCreated[i].iconType = data[i].iconType;
            allSearchSuggestionsCreated[i].content = data[i].content;
            allSearchSuggestionsCreated[i].id = data[i].id;
            allSearchSuggestionsCreated[i].seen = true;
            allSearchSuggestionsCreated[i].Render();

        } else {
            allSearchSuggestionsCreated[i].seen = false;
            allSearchSuggestionsCreated[i].Render();
        }
    }
}
//#endregion


//#region map helper functions
function getESPG(lon, lat) {
    return ol.proj.transform([lat, lon], 'EPSG:4326', 'EPSG:3857');
}
//#endregion

//#region suggestion class
class Suggestion {
    constructor(iconType, content, DOMelement, seen, id) {
        this.iconType = iconType;
        this.content = content;
        this.DOMelement = DOMelement;
        this.seen = seen;
        this.currentVisibility = false;
        this.id = id;
        this.prvRemoved = null;
        var that = this;
        this.DOMelement.addEventListener("click", function () {
            fitdata = places[that.id].loc;
            addSingleRegion(places[that.id].loc);
            addtoSearchPrediction([]);
            document.querySelector("#searchBox").value = places[that.id].name;
            document.querySelector("#sidebarAndSearchContainer").classList += "sidebarActive";
            if (document.querySelector(".sidebar").getBoundingClientRect().width > 700) {
                if (document.querySelector(".allaccdata").classList.contains("small"))
                    document.querySelector(".allaccdata").classList.remove("small");
            } else {
                if (!document.querySelector(".allaccdata").classList.contains("small"))
                    document.querySelector(".allaccdata").classList += " small";
            }
            navigateTo(fitdata);
            addGraph(places[that.id].loc);

        });
    }
    Render() {
        if (this.seen) {
            if (this.currentVisibility == false) {
                if (this.DOMelement.classList.contains("fadeout"))
                    this.DOMelement.classList.remove("fadeout");
                this.prvRemoved = (new Date).getTime();
                this.DOMelement.style.display = "flex";
                this.DOMelement.classList += " fadein";
            }
            //this.DOMelement.querySelector(".icon").src="a.jpg";
            this.DOMelement.querySelector(".content").innerHTML = this.content;
            this.currentVisibility = true;
        } else {
            if (this.currentVisibility == true) {
                if (this.DOMelement.classList.contains("fadein"))
                    this.DOMelement.classList.remove("fadein");
                this.DOMelement.classList += " fadeout";
                const time = (new Date).getTime();
                this.prvRemoved = time;
                setTimeout(() => {
                    if (this.prvRemoved == time)
                        this.DOMelement.style.display = "none";
                }, 500);

            }
            this.currentVisibility = false;
        }
    }
}
//#endregion

//#region Navigation
function navigateTo(data) {
    if (data == null)
        return;
    var minx = 100,
        miny = 100,
        maxx = -1000,
        maxy = -1000;
    for (var i = 0; i < data.length; i++) {
        minx = Math.min(data[i].long, minx);
        miny = Math.min(data[i].lat, miny);
        maxx = Math.max(data[i].long, maxx);
        maxy = Math.max(data[i].lat, maxy);
    }
    var extent1 = getESPG(minx, miny);
    var extent2 = getESPG(maxx, maxy);
    map.getView().fit([extent1[0], extent1[1], extent2[0], extent2[1]], {
        duration: 200,
        padding: [20, (document.querySelector("#sidebarAndSearchContainer").classList.contains("sidebarActive")) ? document.querySelector(".sidebar").getBoundingClientRect().width : 0 + 20, 20, 20]
    });
}
//#endregion

//#region Add Marker
function addaMarker(data) {
    var feature = new ol.Feature({
        geometry: new ol.geom.Point([data.x, data.y])
    })
    var vectorSourcee = new ol.source.Vector({
        features: [feature]
    });
    var vectorLayerr = new ol.layer.Vector({
        source: vectorSourcee,
        style: new ol.style.Style({
            image: new ol.style.Icon(({
                anchor: [0.5, 0.87],
                anchorXUnits: 'fraction',
                anchorYUnits: 'fraction',
                opacity: 0.75,
                src: 'marker.59daef38.png'
            }))
        }),
    });
    map.addLayer(vectorLayerr);
}
//#endregion

//#region Add a circle
function AddaCircle(data) {
    var feature = new ol.Feature({
        geometry: new ol.geom.Point([data.x, data.y])
    })
    var vectorSourcee = new ol.source.Vector({
        features: [feature]
    });
    var vectorLayerr = new ol.layer.Vector({
        source: vectorSourcee,
        style: new ol.style.Style({
            image: new ol.style.Circle(({
                anchor: [0.5, 0.5],
                anchorXUnits: 'fraction',
                anchorYUnits: 'fraction',
                radius: ciclrRad,
                fill: new ol.style.Fill({
                    color: 'rgba(255,0,0,0.65)'
                })
            }))
        }),
    });
    map.addLayer(vectorLayerr);
}
