function addexpanderEvents() {
    document.querySelector(".expander").addEventListener("mousedown", function (e) {
        var prvx = e.x;
        if (!document.querySelector("#sidebarAndSearchContainer").classList.contains("sidebarResizing"))
            document.querySelector("#sidebarAndSearchContainer").classList += " sidebarResizing";
        var prvWidth = document.querySelector(".sidebar").getBoundingClientRect().width;
        document.onmousemove = function (e) {
            var changeX = e.x - prvx;
            var nx = prvWidth - changeX;
            if (nx > 500) {
                navigateTo(fitdata);
                document.querySelector(".sidebar").style.minWidth = nx + "px";
                document.querySelector("#searchBoxContainer").style.minWidth = nx + "px";
                document.querySelector("#whiteRect").style.width = nx + "px";
                if (nx > 700) {
                    if (document.querySelector(".allaccdata").classList.contains("small"))
                        document.querySelector(".allaccdata").classList.remove("small");
                } else {
                    if (!document.querySelector(".allaccdata").classList.contains("small"))
                        document.querySelector(".allaccdata").classList += " small";
                }
            } else if (nx < 500) {
                document.querySelector(".sidebar").style.minWidth = 500 + "px";
                document.querySelector("#searchBoxContainer").style.minWidth = 500 + "px";
                document.querySelector("#whiteRect").style.width = 500 + "px";
            }
            prvWidth = nx;
            prvx = e.x;
            e = e || window.event;
            if (e.stopPropagation) e.stopPropagation();
            if (e.preventDefault) e.preventDefault();
            e.cancelBubble = true;
            e.returnValue = false;
            return false;
        }
        document.onmouseup = function (e) {
            document.onmousemove = null;
            document.onmouseout = null;
            if (document.querySelector("#sidebarAndSearchContainer").classList.contains("sidebarResizing"))
                document.querySelector("#sidebarAndSearchContainer").classList.remove("sidebarResizing");
            graph.generaterangeChanger(0, 23);
            graph.generatePath();
            e = e || window.event;
            if (e.stopPropagation) e.stopPropagation();
            if (e.preventDefault) e.preventDefault();
            e.cancelBubble = true;
            e.returnValue = false;
            return false;
        }
        e = e || window.event;
        if (e.stopPropagation) e.stopPropagation();
        if (e.preventDefault) e.preventDefault();
        e.cancelBubble = true;
        e.returnValue = false;
        return false;
    });

}
