var prvsearchVal = "";

function inputboxEvent() {
    document.querySelector("#searchBox").addEventListener("keyup", function (e) {
        if (e.target.value == "") {
            addtoSearchPrediction([]);
            return;
        }
        if (prvsearchVal == e.target.value)
            return;
        findallIn(e.target.value);
    });
}

function findallIn(text) {
    var res = [];
    for (var j = 0; j < places.length; j++) {
        var i = 0;
        for (i = 0; i < text.length; i++) {
            if (places[j]['name'].length < i || places[j]['name'][i].toLowerCase() != text[i].toLowerCase())
                break;
        }
        if (i == text.length) {
            res.push({
                icon: 0,
                content: places[j]['name'],
                id: j
            });
        }
    }
    addtoSearchPrediction(res, false);
}
