    sortData(startIndex, endIndex) {
        if (startIndex >= endIndex) {
            return;
        }
        const pIndex = this.partition(startIndex, endIndex);
        this.sortData(startIndex, pIndex - 1);
        this.sortData(pIndex + 1, endIndex);
    }
    partition(startIndex, endIndex) {
        const pivot = this.data[endIndex];
        var pIndex = startIndex;
        for (var i = startIndex; i < endIndex; i++) {
            if (this.isFirstNewerDate(pivot, this.data[i])) {
                this.swap(i, pIndex);
                pIndex++;
            }
        }
        this.swap(pIndex, endIndex);
        return pIndex;
    }
    isFirstNewerDate(data1, data2) {
        if (data1.date.year > data2.date.year) {
            return true;
        } else if (data1.date.year < data2.date.year) {
            return false;
        } else {
            if (data1.date.month > data2.date.month)
                return true;
            else if (data1.date.month < data2.date.month)
                return false;
            else {
                if (data1.date.day > data2.date.day)
                    return true;
                else if (data1.date.day < data2.date.day)
                    return false;
                else {
                    if (data1.time.hour > data2.time.hour)
                        return true;
                    else if (data1.time.hour < data2.time.hour)
                        return false;
                    else {
                        if (data1.time.minutes > data2.time.minutes)
                            return true;
                        else if (data1.time.minutes < data2.time.minutes)
                            return false;
                    }

                }
            }
        }
    }
    swap(index1, index2) {
        var tmp = this.data[index1];
        this.data[index1] = this.data[index2];
        this.data[index2] = tmp;
    }
